import Charts from './CommonCharts';

function ChartView(props) {

  let cityCategoryCloseAverageHandleTimeSeries = [];
  let newAddedCities = [];
  for (let cityName in props.cityGraphData.cityCategoryCloseAverageHandleTimeData) {
    newAddedCities = [
      ...newAddedCities, cityName
    ];
    cityCategoryCloseAverageHandleTimeSeries = [
      ...cityCategoryCloseAverageHandleTimeSeries,
      {
        name: cityName,
        type: 'bar',
        barMaxWidth: 20,
        label: {
          position: 'top',
          color: '#333',
          show: true,
          formatter: '{c}'
        },
        data: props.cityGraphData.cityCategoryCloseAverageHandleTimeData[cityName]
      }
    ];
  }

  let citySecondCategoryOpenAverageHandleTimeSeries = [];
  let openAverageHandleTimeCities = [];
  for (let cityName in props.citySecondCategoryOpenAverageHandleTimeData) {
    openAverageHandleTimeCities = [
      ...openAverageHandleTimeCities, cityName
    ];
    citySecondCategoryOpenAverageHandleTimeSeries = [
      ...citySecondCategoryOpenAverageHandleTimeSeries,
      {
        name: cityName,
        type: 'bar',
        barMaxWidth: 20,
        label: {
          position: 'top',
          color: '#333',
          show: true,
          formatter: '{c}'
        },
        data: props.citySecondCategoryOpenAverageHandleTimeData[cityName]
      }
    ];
  }

  return (
    <div>
      <Charts graphData={props.areaGraphData} xAxis={props.allAreas}
        title={{
          title1: "",
          title2: "",
          title3: "",
          title4: "",
          title5: "",
        }}
      />
      <Charts graphData={props.categoryGraphData} xAxis={props.allCategories}
        title={{
          title1: "",
          title2: "",
          title3: "",
          title4: "",
          title5: "",
        }}
      />
      <Charts graphData={props.cityGraphData} xAxis={props.allCities}
        title={{
          title1: "",
          title2: "",
          title3: "",
          title4: "",
          title5: "",
        }}
      />
      <Charts graphData={props.projectGraphData} xAxis={props.allProjects}
        title={{
          title1: "",
          title2: "",
          title3: "",
          title4: "",
          title5: "",
        }}
      />
      <Charts graphData={props.stageGraphData} xAxis={props.allStages}
        title={{
          title1: "",
          title2: "",
          title3: "",
          title4: "",
          title5: "",
        }}
      />
    </div>
  );
}

export default ChartView;
