import React, { Component } from 'react';
import './App.css';
import moment from 'moment';
import 'moment/locale/zh-cn';
import {
	KEY_REPORT_TIME,
	COL_PROJECT_STAGE,
	COL_PROJECT,
	COL_CITY_COMPANY,
	COL_AREA,
	COL_REPORT_TIME,
	CATEGORIES,
	COL_DUTY_WORKER
} from './constants';

import { Row, Col, Tabs } from 'antd';
import DataForm from './components/DataForm';
import ChartView from './components/ChartView';
import ProjectView from './components/ProjectView';

import {
	readExcelData
} from './utils';

import { connect } from 'react-redux';
import { addCategories } from './store/actions';

import * as Util from './functions/utils';

import MainView from './components/MainView';

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			stageGraphData: {},
			cityGraphData: {},
			projectGraphData: {},
			areaGraphData: {},
			categoryGraphData: {}
		}
	}

	start = (fileList, startTime, endTime) => {
		const { addCategories } = this.props;
		this.setState({ loading: true });
		readExcelData(fileList).then((data) => {
			let allStages = [], allProjects = [], allCities = [], allAreas = [], allWorkers = [];
			// get all categories and ticket states
			for (let ticket of data) {
				// get all stages
				let stage = Util.getItem(ticket[COL_PROJECT_STAGE]);
				if (allStages.indexOf(stage) === -1) {
					allStages = [
						...allStages,
						stage
					];
				}
				// get all projects
				let project = Util.getItem(ticket[COL_PROJECT]);
				if (allProjects.indexOf(project) === -1) {
					allProjects = [
						...allProjects,
						project
					];
				}
				// get all cities
				let city = Util.getItem(ticket[COL_CITY_COMPANY]);
				if (allCities.indexOf(city) === -1) {
					allCities = [
						...allCities,
						city
					];
				}
				// get all area
				let area = Util.getItem(ticket[COL_AREA]);
				if (allAreas.indexOf(area) === -1) {
					allAreas = [
						...allAreas,
						area
					];
				}
				// get all workers
				let worker = Util.getItem(ticket[COL_DUTY_WORKER]);
				if (allWorkers.indexOf(worker) === -1) {
					allWorkers = [
						...allWorkers,
						worker
					];
				}
			}

			let allCategories = CATEGORIES;

			this.setState({
				allStages,
				allProjects,
				allCities,
				allAreas,
				allCategories,
				allWorkers
			});

			addCategories(allCategories);

			let yearStartTime = moment(startTime).set({ month: 0, date: 1, hour: 0, minute: 0, second: 0 });
			let periodTickets = data.filter(ticket => {
				let ticketReportTime = moment(ticket[COL_REPORT_TIME]);
				return ticketReportTime >= startTime && ticketReportTime <= endTime;
			});
			let beforePeriodTickets = data.filter(ticket => {
				let ticketReportTime = moment(ticket[COL_REPORT_TIME]);
				return ticketReportTime >= yearStartTime && ticketReportTime < startTime;
			});
			let thisYearTotalTickets = data.filter((ticket) => {
				let reportTime = moment(ticket[KEY_REPORT_TIME]);
				return reportTime <= endTime && reportTime >= yearStartTime;
			});
			let lastYearBeforePeriodTickets = data.filter(ticket => {
				let ticketReportTime = moment(ticket[COL_REPORT_TIME]);
				return ticketReportTime < yearStartTime
			});

			// 
			let workerData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
				lastYearBeforePeriodTickets, startTime, endTime, COL_DUTY_WORKER, allWorkers);
			this.setState({
				workerData
			});

			// 
			let stageData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
				lastYearBeforePeriodTickets, startTime, endTime, COL_PROJECT_STAGE, allStages)
			this.setState({
				stageData
			});

			// 
			let projectData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
				lastYearBeforePeriodTickets, startTime, endTime, COL_PROJECT, allProjects)
			this.setState({
				projectData
			});

			// 
			let cityData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
				lastYearBeforePeriodTickets, startTime, endTime, COL_CITY_COMPANY, allCities)
			this.setState({
				cityData
			});

			// 
			let areaData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
				lastYearBeforePeriodTickets, startTime, endTime, COL_AREA, allAreas)
			this.setState({
				areaData
			});

			// 
			let stageGraphData = Util.formatGraphData(stageData, allStages);
			this.setState({
				stageGraphData
			})

			let projectGraphData = Util.formatGraphData(projectData, allProjects);
			this.setState({
				projectGraphData
			});

			let cityGraphData = Util.formatGraphData(cityData, allCities);
			this.setState({
				cityGraphData
			});

			let areaGraphData = Util.formatGraphData(areaData, allAreas);
			this.setState({
				areaGraphData
			});

			// 
			let categoryGraphData = Util.formatCategoryGraphData(areaData, allCategories, allAreas[0]);
			this.setState({
				categoryGraphData
			});

			this.setState({
				loading: false
			});
		});
	}

	render() {
		return (
			<div>
				<h2 className="text--center heading"></h2>
				<DataForm
					onStart={this.start}
					loading={this.state.loading}
				/>
				<Row style={{ margin: '40px 0' }}>
					<Col span={22} offset={1}>
						<Tabs type="card">
							<Tabs.TabPane tab="" key="">
								<ChartView
									stageGraphData={this.state.stageGraphData}
									projectGraphData={this.state.projectGraphData}
									cityGraphData={this.state.cityGraphData}
									areaGraphData={this.state.areaGraphData}
									categoryGraphData={this.state.categoryGraphData}
									allStages={this.state.allStages}
									allProjects={this.state.allProjects}
									allCities={this.state.allCities}
									allAreas={this.state.allAreas}
									allCategories={this.state.allCategories}
								/>
							</Tabs.TabPane>
							<Tabs.TabPane tab="" key="">
								<ProjectView
									data={this.state.areaData}
									projects={this.state.allAreas}
									name=""
								/>
							</Tabs.TabPane>
							<Tabs.TabPane tab="" key="">
								<ProjectView
									data={this.state.cityData}
									projects={this.state.allCities}
									name=""
								/>
							</Tabs.TabPane>
							<Tabs.TabPane tab="" key="">
								<ProjectView
									data={this.state.projectData}
									projects={this.state.allProjects}
									name=""
								/>
							</Tabs.TabPane>
							<Tabs.TabPane tab="" key="">
								<ProjectView
									data={this.state.stageData}
									projects={this.state.allStages}
									name=""
								/>
							</Tabs.TabPane>
							<Tabs.TabPane tab="" key="">
								<ProjectView
									data={this.state.workerData}
									projects={this.state.allWorkers}
									name=""
								/>
							</Tabs.TabPane>
						</Tabs>
					</Col>
				</Row>
			</div>
		);
	}
}

const mapDispatchToProps = {
	addCategories,
}

const ConnectedApp = connect(null, mapDispatchToProps)(App);

export default ConnectedApp;
